<template>
  <div v-if="addressInfo" class="order-detail">
    <scroll class="order-scroll">
      <div>
        <purchase-address
          :address-info="addressInfo"></purchase-address>
        <div class="wrapper">

          <order-item @detail="toDetail(i)" :item-data="i" v-for="i in addressInfo.orderDetails"
                      :key="i.id"></order-item>
          <div class="info"><span>运费(袋鼠自营商城)</span><span>+￥0.0</span></div>
          <div class="info"><span>订单金额</span><span>{{price(addressInfo.payTb,addressInfo.payCash)}}</span>
          </div>
        </div>
        <div class="order-info">
          <div class="title">订单信息</div>
          <div>订单编号：{{addressInfo.id}}</div>
          <div v-if="addressInfo.payTime">
            付款时间：{{formdate(addressInfo.payTime)}}
          </div>
          <div v-if="addressInfo.logisticsCompany">
            {{addressInfo.logisticsCompany}}：{{addressInfo.logisticsOrderId}}
          </div>
        </div>
      </div>
    </scroll>
    <div class="bottom-bar">
      <span @click="toServer"><i class="iconfont"
                                 style="color: #ff4800;vertical-align: middle">&#xe629;</i> 联系客服</span>
      <div class="order-item-bottom">
        <div @click="logistics(addressInfo.id)"
             v-show="addressInfo.isPay==='Y'&&addressInfo.isShip==='Y'&&addressInfo.isVirtualOrder==='N'&&addressInfo.isConfirmReceipt==='N'">
          查看物流
        </div>
        <div
          v-show="addressInfo.isConfirmReceipt==='N'&&addressInfo.isVirtualOrder==='N'&&addressInfo.isShip==='Y'"
          class="pay" @click="confirmReceipt(addressInfo.id)">确认收货
        </div>
        <div @click="quxiao(addressInfo.id)" v-show="addressInfo.isPay==='N'">
          取消订单
        </div>
        <div v-show="addressInfo.isPay==='N'" @click="toPay(addressInfo.id)"
             class="pay">立即支付
        </div>
        <div class="dfh"
             v-show="addressInfo.isShip==='N'&&addressInfo.isPay==='Y'">待发货
        </div>
        <div class="dfh" v-show="addressInfo.isConfirmReceipt==='Y'">已完成</div>
      </div>
    </div>
    <logistics @close="close" :logistics-data="logisticsData"
               v-show="showLogistics"></logistics>

  </div>
</template>

<script>
  import * as core from '../../api/orderForm'
  import PurchaseAddress from '../../base/purchase-address/purchase-address'
  import OrderItem from '../../base/order-item/order-item'
  import Scroll from '../../base/scroll/scroll'
  import Logistics from '../../base/logistics/logistics'
  import tool from '../../common/js/util'

  export default {
    name: 'order-detail',
    data() {
      return {
        addressInfo: null,
        logisticsData: null,
        showLogistics: false
      }
    },
    components: {
      PurchaseAddress,
      OrderItem,
      Scroll,
      Logistics
    },
    created() {
      document.title = this.$route.meta.title     // console.log(this.$route.query.cartIds)
      this.getDetail()
    },
    methods: {
      toDetail(e){
        this.$router.push({path:'/commodityDetails',query:{commodityId:e.itemId}})
      },
      toServer() {
        window.location.href = 'https://www16.53kf.com/m.php?cid=72187208&style=1&keyword=http%3A%2F%2Ftest.51jujibao.com%3A8080%2F&referer=http%3A%2F%2Fm.51jujibao.com%2Fv2%2Findex.html%23%2Fcenter&guest_id=10562525909006&tpl=crystal_blue&uid=1827a2a86496d5a9c9a1617ea6489f0b&u_stat_id=&talktitle=%E4%B8%AA%E4%BA%BA%E4%B8%AD%E5%BF%83&tfrom=50&device=&zdkf_type=1&kf=&kflist=off'
      },
      logistics(e) {
        core.logistics({orderId: e}).then(res => {
          if (res.code && '00' === res.code) {
            if (res.result.success){
              res.result.transferDetails=res.result.transferDetails.reverse();
              this.logisticsData=res.result;
              this.showLogistics=true
            }else {
              this.$toastBox.showToastBox(res.result.message)
            }
          } else if (res.code && '01' === res.code) {
            tool.toWeiXinLogin(window.location.href)
          } else {
            this.$toastBox.showToastBox(res.message)
          }
        }).catch(e => {
          this.$toastBox.showToastBox(e)
        })
      },
      close() {
        this.showLogistics = false
      },
      formdate(e) {
        return tool.formatDate(e)
      },
      confirmReceipt(id) {
        core.confirmReceipt({orderId: id}).then(res => {
          if (res.code && '00' === res.code) {
            this.getDetail()
          } else if (res.code && '01' === res.code) {
            tool.toWeiXinLogin(window.location.href)
          } else {
            this.$toastBox.showToastBox(res.message)
          }
        }).catch(e => {
          this.$toastBox.showToastBox(e)
        })
      },
      quxiao(id) {
        core.quxiao({orderId: id}).then(res => {
          if (res.code && '00' === res.code) {
            this.$router.push({path: '/orderForm'})
          } else if (res.code && '01' === res.code) {
            tool.toWeiXinLogin(window.location.href)
          } else {
            this.$toastBox.showToastBox(res.message)
          }
        }).catch(e => {
          this.$toastBox.showToastBox(e)
        })
      },
      toPay(id) {
        core.toPay({orderId: id}).then(res => {
          if (res.code && '00' === res.code) {
            window.location.href = res.result.payUrl
          } else if (res.code && '01' === res.code) {
            tool.toWeiXinLogin(window.location.href)
          } else {
            this.$toastBox.showToastBox(res.message)
          }
        }).catch(e => {
          this.$toastBox.showToastBox(e)
        })
      },
      getDetail() {
        core.detail({orderId: this.$route.query.orderId}).then(res => {
          if (res.code && '00' === res.code) {
            this.addressInfo = res.result
          } else if (res.code && '01' === res.code) {
            tool.toWeiXinLogin(window.location.href)
          } else {
            this.$toastBox.showToastBox(res.message)
          }
        }).catch(e => {
          this.$toastBox.showToastBox(e)
        })
      },
      price(a, b) {
        return tool.priceStr(a, b)
      },
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../../common/stylus/iconfont.styl"
  @import "../../common/stylus/mixin.styl"
  .order-detail
    .order-scroll
      z-index -1
      position fixed
      top 0
      left 0
      right 0
      bottom 3.063rem
      background-color #f5f5f5
      .wrapper
        background-color #fff
        padding 0.75rem 4.27%
        margin 0.75rem 0
        .info
          display flex
          justify-content space-between
          font-size 0.75rem
          line-height 1.75rem
          color #333
      .order-info
        background-color #fff
        padding 0 4.27% 1.031rem
        margin-top 0.625rem
        font-size 0.75rem
        line-height 1.344rem
        color #999
        .title
          font-size 0.813rem
          line-height 1
          padding 0.719rem 0 0.719rem 0.594rem
          position relative
          color #333
          &:before
            position absolute
            content ''
            left 0
            width 0.094rem
            height 0.813rem
            background-color #ff4800
    .bottom-bar
      border-top 1px solid #e6e6e6
      position fixed
      left 0
      background-color #fff
      bottom 0
      width 100%
      height 3.063rem
      line-height 3.063rem
      box-sizing border-box
      display flex
      justify-content space-between
      padding 0 4.27%
      span
        extend-click()
        font-size: 0.75rem
        color: #999
      .order-item-bottom
        display flex
        justify-content flex-end
        div
          margin-top 0.719rem
          height 0.75rem
          line-height 1
          padding 0.313rem 0.719rem
          font-size 0.75rem
          border-radius 0.688rem
          margin-left 1.063rem
          color #999
          border 1px solid #999
        .pay
          color #ff4800
          border-color #ff4800
        .dfh
          border 0
          font-size 0.875rem
</style>



