<template>
  <div class="logistics" v-if="logisticsData">
    <div class="wrapper">
      <div class="logistics-top">
        <div class="title">
          物流详情
        </div>
        <scroll class="logistics-scroll">
          <div style="padding:0 1rem;position: relative">
            <div v-for="(item,index) in logisticsData.transferDetails" class="logistics-item">
              <div class="time"><span
                style="font-size: 0.813rem;color: #666">{{getTime(item.datetime)[0]}}</span><br /><span
                style="font-size: 0.688rem;color: #999">{{getTime(item.datetime)[1]}}</span></div>
              <div class="icon">
                <img v-show="index===0" src="./images/icon-dd.png" alt="">
                <img v-show="index!==0&&index!==logisticsData.transferDetails.length-1" src="./images/icon-yx.png" alt="">
                <img v-show="index===logisticsData.transferDetails.length-1" src="./images/icon-ck.png" alt="">
              </div>
              <div class="info">
                <div v-show="index!==0" class="mask-top"></div>
                <div v-show="index!==logisticsData.transferDetails.length-1" class="mask-bottom"></div>
                {{item.remark}}
              </div>
            </div>
          </div>
        </scroll>
        <div class="tip">{{logisticsData.expressDeliveryCompany}}</div>
      </div>
      <div class="close">
        <i @click="close" class="iconfont">&#xe63f;</i>
      </div>
    </div>
  </div>
</template>

<script>
  import Scroll from '../../base/scroll/scroll'
  import tool from '../../common/js/util'
  export default {
    name: 'logistics',
    props:{
      logisticsData:{
        type:Object,
        default:''
      }
    },
    methods:{
      close(){
        this.$emit('close')
      },
      getTime(e){
        return tool.formatDate(e,'1').split(' ')
      }
    },
    components: {
      Scroll
    }
  }
</script>

<style lang="stylus" scoped>
  @import "../../common/stylus/iconfont.styl"
  @import "../../common/stylus/mixin.styl"
  .logistics
    position fixed
    top 0
    left 0
    right 0
    bottom 0
    background-color rgba(0, 0, 0, 0.8)
    .wrapper
      width 85.33%
      margin 4.688rem auto 0
      position relative
      .logistics-top
        background-color #fff
        overflow hidden
        border-radius 0.25rem
      .title
        height 4.062rem
        line-height 4.062rem
        text-align center
        color #fff
        font-size 1.125rem
        background url("./images/title-bg.png") no-repeat center / cover
      .logistics-scroll
        height 23.875rem
        overflow hidden
        .line
          position absolute
          background #000
          width 1px
          height 100%
          left 4.4875rem
        .logistics-item
          padding 1rem 0
          font-size 0.75rem
          line-height 1.125rem
          position relative
          padding-left 25.46%
          .mask-top
            top 0
            width 1px
            height 50%
            position absolute
            left 3.4875 rem
            background-color #e6e6e6
          .mask-bottom
            width 1px
            height 50%
            top 50%
            position absolute
            left 3.4875 rem
            background-color #e6e6e6
          .time
            position absolute
            left 0
            top 50%
            transform translateY(-50%)
          .icon
            z-index 999
            left 2.8rem
            top 50%
            transform translateY(-50%)
            position absolute
            width 1.375rem
            height 1.375rem
      .tip
        text-align center
        color #999
        font-size 0.75rem
        width 100%
        height 2.063rem
        line-height 2.063rem
      .close
        padding-top 1rem
        color #fff
        width 100%
        position absolute
        text-align center
        i
          extend-click()
          font-size 1.5rem
</style>



